/* company.js */
.warp, ul, a, h1{
  margin: 0; padding: 0; 
  box-sizing: border-box; 
  vertical-align: top; 
  text-decoration: none; 
  list-style: none;
}
.warp{
  position: relative;
  margin-top: 20px;
}
.warp header{
  position: absolute;
  left: 0; top: 0;
  background-color: #192131;
  width: 15%; height: 95vh;
}
.warp header h1{
  color: #fff;
  font-size: 2em;
  text-align: right;
  padding: 20px 40px;
  background-color:#23234b;
}
.warp header .menu_list{
  margin-bottom: 50px;
}
.warp header .menu_list li{
  color: #eee;
  padding: 10px 40px;
  text-align: right;
  font-size: 1.3em;
}
.warp header .menu_list li.active{
  background: #ddd;
  color: #507d88;
}

.warp main{
  position: absolute;
  width: 85%;
  right: 0; top: 0;
  overflow: hidden;
  
}
.warp main ul{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  /* height: 100vh;   */
}
.warp main ul li{
  display: inline-block;
  width: 25%; height: 223px;
  background-color: darkgray;
}
.warp main ul li img{
  width: 100%;
}


/* ÎÅù */


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
